import React, { useState, useEffect } from 'react';

import List from './components/List';
import Sidebar from './components/Sidebar';

import styles from './css/App.module.css';

function App() {
    const [data, setData] = useState(JSON.parse(localStorage.getItem('data') || '[]'));
    useEffect(() => localStorage.setItem('data', JSON.stringify(data)), [data]);

    const update = (index, newData) => {
        let next = data.slice();
        if (index === -1) {
            next.push(newData);
        } else {
            next[index] = newData;
        }
        setData(next);
    };

    const remove = (index) => {
        let next = data.slice();
        next.splice(index, 1);
        setData(next);
    };

    // Check if no one is selected
    let none = !data.some((x) => x.selected);
    // If no one is selected, it cannot be that everyone is selected
    let all = !none && data.every((x) => x.selected);
    let selected = !all && !none ? 'indeterminate' : all;
    const changeCheck = () => {
        if (selected === true) {
            setData(data.map((x) => ({ ...x, selected: false })));
        } else {
            setData(data.map((x) => ({ ...x, selected: true })));
        }
    }

    return (
        <div className={styles.root}>
            <Sidebar data={data} />
            <List people={data} onUpdate={update} onRemove={remove} selected={selected} onCheck={changeCheck} />
        </div>
    );
}

export default App;
